.App {
  text-align: center;
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.elements {
  width: 100%;
align-items: center;
display: flex;
flex-direction: column;
margin-top: 0%;

}

.buttonContainer {
  display: flex;
  flex-direction: row;
  gap: 20px;
}

.componentContainer2{
  position: fixed;
  margin-top: 10%;
  margin-left: 40%;
  width: 30%;
}

.componentContainer1 {
  position: fixed;
  margin-top: 10%;
  margin-left: -30%;
  width: 30%;
}

.buttons {
  background-color: blue;
  color: white;
  padding: 10px 20px;
  border: none;
  border-radius: 5px;
  cursor: pointer;
  font-size: 16px;
}